form{
  input[type="text"],
  input[type="password"],
  input[type="email"],
  textarea,
  select{
    border:none;
    -webkit-appearance:none;
    background:$form-input-color;
    height:20px;
    padding:10px;
  }
  textarea{
    height:auto;
  }
  input[type="submit"]{
    @include button();
    padding:10px;
  }
}